import React from 'react'
import axios from 'axios'
import {connect} from 'react-redux'
import PropTypes from 'prop-types'
import { withRouter } from 'react-router-dom'
import SparkMD5 from 'spark-md5'
// import JSEncrypt from 'jsencrypt'

import config from '../../../utils/config.js'
import { alertMessageAction } from '../../../flux/actions/messageAction'
import {setAuthToken, setCurrentUser} from '../../../utils/setAuth'
import { isEmpty } from '../../../utils/validator'
const HOST = config.HOST

var loging = false // 

class Account extends React.Component {
  constructor(props) {
    super(props);
    this.state = {
      account: '',
      password: '',
      errors: {}
    }
    this.inputChange = this.inputChange.bind(this);
    this.inputFocus = this.inputFocus.bind(this);
  }

  componentDidMount() {
    window.document.title = '--'
  }

  inputChange(e) {
    this.setState({
      [e.target.name]: e.target.value
    });
  }

  inputFocus(e) {
    this.setState({
      errors: {...this.state.errors, [e.target.name]: ''}
    })
  }

  formSubmit(e) {
    e.preventDefault()
    if (loging) {
      return this.props.alertMessageAction('')
    }
    let errors = {};
    if (isEmpty(this.state.account)) {
      errors.account = '';
    }
    if (!/^[\w,';)(!~`\/\\{}"<>?+-=_.]{6,25}$/.test(this.state.password)) {
      errors.password = '';
    }

    if (Object.keys(errors).length > 0) {
      return this.setState({
        errors: errors
      })
    }
    loging = true
    var target = this.refs.submit
    target.innerText = ''

    axios.get(`${HOST}/api/users/rsa_public?account=${this.state.account}`)
    .then(({ data }) => {
      if (!data.success) {
        return this.setState({
          errors: { submit: data.message }
        })
      }
      // md5
      var encrypt = new window.JSEncrypt()
      encrypt.setPublicKey(data.payload)
      let password = encrypt.encrypt(SparkMD5.hash(this.state.password))
      // let password = SparkMD5.hash(this.state.password)
      axios.post(`${HOST}/api/users/login`, {
        account: this.state.account,
        password
      }).then(({ data }) => {
        loging = false
        if (!data.success) {
          return this.setState({
            errors: { submit: data.message }
          })
        }
        setAuthToken(data.payload.token)
        setCurrentUser(data.payload.user)
        this.props.history.push(this.props.prev)

      }).catch(err => {
        loging = false
        target.innerText = ''
        this.setState({
          errors: { submit: err.message || '' }
        })
      })

    }).catch(err => {
      loging = false
      target.innerText = ''
      this.setState({
        errors: { submit: err.message || '' }
      })
    })
  }
  render() {
    return (
      <div>
        <form className="l-input-wrap" onSubmit={this.formSubmit.bind(this)} method="POST">
          <div className="log-input1">
            <label htmlFor="account" style={{
              background: `url(${HOST}/image/ui/${this.state.errors.account ? 'account_failed.png' : 'account.png'}) center center / 30px 30px no-repeat`
            }}></label>
            <input
              onFocus={this.inputFocus}
              onChange={this.inputChange}
              value={this.state.account}
              placeholder=" / "
              name="account"
              id="account"
              autoComplete="off"
              type="text"/>
          </div>
          {
            this.state.errors.account && <div className="acc-log-err">{this.state.errors.account}</div>
          }
          <div className="log-input2">
            <label htmlFor="password" style={{
              background: `url(${HOST}/image/ui/${this.state.errors.password ? 'lock_failed.png' : 'lock.png'}) center center / 30px 30px no-repeat`
            }}></label>
            <input
              onFocus={this.inputFocus}
              type="password"
              placeholder=""
              name="password"
              id="password"
              onChange={this.inputChange}
              value={this.state.password}
            />
          </div>
          {
            this.state.errors.password && <div className="acc-log-err">{this.state.errors.password}</div>
          }
          <input ref="submit" className="account-log-submit" type="submit" value=""/>
          {
            this.state.errors.submit && <div className="acc-log-err acc-log-err-submit">{this.state.errors.submit}</div>
          }
        </form>
      </div>
    )
  }
}
Account.propTypes = {
  auth: PropTypes.object.isRequired,
}
const mapStateToProps = state => {
  return {
    auth: state.auth
  }
}
export default withRouter(connect(mapStateToProps, { alertMessageAction })(Account))
